import {WidgetAction} from "../hoc";
import {WidgetActionType} from "../../../models";
import {JsActionModel} from "../../../models";

// Window service adapted to TS
import {runAdapted} from "../../../adapters/WindowAdapter";

// TODO: keep tslint happy -- remove logs
const log = console.log;

export const JS_ACTION: WidgetAction = {
    type: WidgetActionType.Js,
    execute: (action?: JsActionModel) => { 
        log("Js Action Executed"); 
        log("action: ", action);
        if (action) {
            if (action.code) {
                log("Js: eval code");
                runAdapted(action.code);               
            }
        }
    }
};


