import {WidgetAction} from "../hoc";
import {WidgetActionType} from "../../../models";
import {CustomJsActionModel} from "../../../models";
import {injectParameters, checkParameter} from "./util";
import {runAdapted} from "../../../adapters/WindowAdapter";
import {each} from "underscore";

const log = console.log; // TODO: remove for tslint

/**
 * Checks if the supplied code template is valid/runnable for the supplied parameters, returning null if valid.
 *
 * @param  {String}  template    The code template to check after <tt>parameters</tt> have been injected.
 * @param  {Map}    [parameters] The parameters to optionally check; can be undefined.
 *
 * @return {Error} The error if invalid; otherwise null.
 */
const checkRunnableCode = (template: string, parameters: {[key: string]: string}) => {
    try {
        // Parameter names and values must be valid...
        if (parameters) {
            each(parameters, (value: string, name: string) => {
                const error = checkParameter(name, value);
                if (error) {
                    throw error;
                }
            });
        }

        // Inject any arguments, allowing them to be empty...
        const code: string | null = injectParameters(template, parameters);

        try {
            // tslint:disable-next-line:no-unused-expression
            new Function(code);
        } catch (syntaxError) {
            throw new Error(syntaxError);
        }

        // And JS must be able to be serialized to/from JSON...
        const result = JSON.parse(JSON.stringify({code}));
        if (result.code !== code) {
            throw new Error("Code cannot be serialized to/from json.");
        }
        return null; // no error
    } catch (error) {
        return error;
    }
};

export const CUSTOM_JS_ACTION: WidgetAction = {
    type: WidgetActionType.CustomJs,
    execute: (action?: CustomJsActionModel) => {
        if (action) {
            try {
                if (checkRunnableCode(action.code, action.parameters) === null) {
                    runAdapted(injectParameters(action.code, action.parameters));
                }               
            } catch (error) {
                log("code not runnable", error); // TODO: port Logger
                return null;
            }
        }
    }
};    


