import {isBlank} from "../../../../../util/StringUtils";

// Register to search for illegal characters in parameters names and values, 
// matching a similar one in the API...
// TODO: we should disallow pre and postfixed spaces, like /(^\s|\s$|[^a-zA-Z0-9_\- ])/
const PARAMETER_NAME = /([^a-zA-Z0-9_\- ])/; 
const PARAMETER_VALUE = /([^a-zA-Z0-9_.*+\-:/\\=?#&])/;

/**
 * Checks the specified parameter name and value and returns an error message if illegal,
 * otherwise returns null.
 *
 * @param  {String} name    The parameter name to check; cannot be empty.
 * @param  {String} [value] The parameter value to check; can be empty or not supplied at all, e.g. for
 *                          validation in the Editor when the Custom JS Action prototype is set up.
 *
 * @return {String} Returns null if not error, otherwise an error message.
 */

export const checkParameter = (name: string, value?: string) => {
    let match = PARAMETER_NAME.exec(name);

    if (match) {
        return `Invalid parameter name: '${match[1]}' in '${name}'`;
    } else if (isBlank(name)) {
        return "Parameter name cannot be empty";
    }

    if (value) {
        match = PARAMETER_VALUE.exec(value);
        if (match) {
            return `Invalid parameter value: '${match[1]}' in ${value}`;
        }
    }
    
    return null;
};


