import * as React from "react";

import {WidgetSize} from "../types";
import {HOC} from "../../../../hoc";
import {ImageWidgetModel} from "../../../../models";
import {Size} from "../../../../geom";
import {getAppropriateBoxSize} from "../util/ImageBoxSize";

const DEFAULT_IMG = "images/transparent-pixel.gif";

export type WithImageSizeEditorProps = ImageWidgetModel & WidgetSize;
type OutputProps = ImageWidgetModel;

const computeImageUrlWithSize = (src: string, frame: Size, imageOriginalSize: Size) => {
    const extension = src.split(".").pop();
    const extensionFinalName = extension ? `.${extension}` : "";
    const urlNoExtension = extension ? src.replace(`.${extension}`, "") : src;
    const boxSize = getAppropriateBoxSize(imageOriginalSize, frame);

    return !boxSize
        ? `${urlNoExtension}-optimized${extensionFinalName}`
        : `${urlNoExtension}_${boxSize.width}x${boxSize.height}${extensionFinalName}`;
};

export const withCreatorImageSize: HOC<OutputProps, WithImageSizeEditorProps> = (BaseComponent) => ({
    src,
    frame,
    ...props
}) => {
    const imageOriginalSize = new Size(props.originalImageWidth, props.originalImageHeight);
    const srcWithSize = !src ? DEFAULT_IMG : computeImageUrlWithSize(src, frame, imageOriginalSize);

    return <BaseComponent {...props} src={srcWithSize} />;
};



